<template>
  <div class="page">
    <div class="head flex-center">
      <div class="head-back">
        <!-- <img src="@/assets/image/new/back.png" alt=""> -->
      </div>
      <div class="head-title">合約管理</div>
      <div class="head-right">
        <div class="head-wallet" v-if="!address" @click="initWallet">鏈接錢包</div>
        <div class="head-wallet" v-else>{{address.substring(0,4)+'...'+address.substring(address.length-4,address.length)}}</div>
      </div>
    </div>
    <div class="set-content">
      <div class="set-item flex-between">
        <div class="dqcan">gas數量</div>
        <input class="flex-item" type="number" v-model="gasNumber" placeholder="請輸入">
      </div>
      <div class="Precision">精度: 18</div>
      <el-button @click="confimNum()">保存</el-button>
      <div class="set-item flex-between">
        <div class="dqcan">gas接收地址</div>
        <input class="flex-item" type="text" v-model="gasAddress" placeholder="請輸入">
      </div>
      <el-button @click="confimAddress()">保存</el-button>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs, computed, watch } from "vue";
import { useStore } from "vuex";
import config from "@/utils/config";
import web3Data from "@/utils/initWeb3";
import { ElMessage, ElNotification } from 'element-plus'
import MainChainDataAbi from "@/utils/MainChainDataAbi";
import Bignumber from "bignumber.js";

export default({
  setup () {
    const store = useStore()
    const data = reactive({
      local: computed(() => store.state.locale),
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      MainChainDataContract: null,
      gasNumber: null,
      Price: '',
      gasAddress: '',
      initContract: () => {
        data.MainChainDataContract = new data.web3.eth.Contract(MainChainDataAbi, config.MainChainDataAddress)
      },
      confimNum: async()=> {
        if(data.gasNumber == 0 || !data.gasNumber) {
          ElNotification.error({
            title: '數量需大於0',
            duration: 3000
          })
          return
        }
        let gasNum = new Bignumber(data.gasNumber).multipliedBy(Math.pow(10, 18)).toFixed()
        await data.MainChainDataContract.methods.setgasPrice(gasNum).estimateGas({from: data.address})
        await data.MainChainDataContract.methods.setgasPrice(gasNum).send({from: data.address})
        .then(() => {
          ElNotification.success({
            title: '成功',
            duration: 3000
          })
          data.gasNumber = null
        }).catch(() => {
          ElNotification.error({
            title: '失敗',
            duration: 3000
          })
        })
      },
      confimAddress: async()=> {
        let isAddress = await data.web3.utils.isAddress(data.gasAddress)
        if (!isAddress) {
          ElNotification.error({
            title: '請輸入正確的地址',
            duration: 3000
          });
          return;
        }
        await data.MainChainDataContract.methods.setFuncAddress(data.gasAddress).send({from: data.address})
        .then(() => {
          ElNotification.success({
            title: '成功',
            duration: 3000
          })
          data.gasAddress = null
        }).catch(() => {
          ElNotification.error({
            title: '失敗',
            duration: 3000
          })
        })
      },
      initWallet: () => {
        web3Data.initWeb3()
      }
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
})
</script>
<style lang="scss" scoped>
.page{
  background: #060303;
}
.head{
  background: transparent;
  height: 44px;
  line-height: 44px;
  text-align: center;
  font-size: 18px;
  font-weight: bold;
  color: #03090F;
  padding: 0 15px;
  margin-bottom: 15px;
  position: relative;
  .head-back{
    img{
      width: 9px;
      height: 16px;
    }
  }
  .head-title{
    font-size: 18px;
    font-weight: bold;
    color: #FFFFFF;
  }
  .head-wallet{
    font-size: 15px;
    font-weight: bold;
    color: #FFFFFF;
  }
  .head-right{
    position: absolute;
    right: 15px;
    top: 0;
    max-width: 100px;
  }
}
.set-content{
  padding: 15px;
  background: #F4F4F4;
  border-radius: 15px 15px 0px 0px;
  .dqcan{
    font-size: 15px;
    font-weight: bold;
    color: #333333;
  }
  .set-item{
    background: #FFFFFF;
    border-radius: 10px;
    padding: 5px 12px;
    height: 55px;
    line-height: 55px;
  }
  input{
    // width: 80%;
    // height: 50px;
    background: transparent;
    border: 0;
    text-align: right;
    font-size: 15px;
    color: #333;
    margin-left: 10px;
  }
  .Precision{
    text-align: right;
    font-size: 15px;
    color: #333;
    margin-top: 10px;
  }
  .el-button{
    width: 100%;
    height: 50px;
    margin: 30px 0 45px;
    background: linear-gradient(90deg, #FAB742, #FCCC57);
    border-radius: 25px;
    font-size: 18px;
    font-weight: bold;
    color: #333333;
  }
}
</style>
